.chat {
    flex: 1;
    display: flex;
    flex-direction: column;
    
}

.chat_header {
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat__headerInfo {
    flex: 1;
    padding-left: 20px;
}

.chat__headerInfo > h3 {
    margin-bottom: 3px;
    font-weight: 500;
    color: #a876aa;
    font-weight: bolder;
}

.chat__headerInfo > p {
    color: gray;
}

.chat__message {
    display: flex;
    position: relative;
    font-size: 16px;
    padding: 10px;
    border-radius: 10px;
    width: fit-content;
    background-color: #a876aa;
     color: #eee;
    margin-bottom: 30px;

}

.chat__name {
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}

.chat__name > .MuiAvatar-root{
    margin: 10px;
    /* color: #a876aa; */
}

.chat__receiver{
    margin-left: auto;
    background-color: #dcf8c6;
    color: gray;
    display: flex;
    flex-direction: row-reverse;
}

.chat__timestamp {
    margin-left: 10px;
    font-size: xx-small;
}

.chat__headerRight {
    display: flex;
    justify-content: space-between;
    max-width: 100px;
}

.chat__headerRight  > .MuiAvatar-root {
    color: #a876aa;
}

.chat_body {
    flex: 1;
    /* background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png"); */
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: scroll;
}

.chat_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border: 1px solid lightgray;
}

.chat_footer > form {
    flex: 1;
    display: flex;
}

.chat_footer > form > input{
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    outline: none;
    background-color: #eee;

}

.chat_footer > form > button{
    display: none;
}

.chat_footer > .MuiSvgIcon-root {
    padding: 10px;
    color: gray;
}

